// @flow
import Phaser from 'phaser';
import { getResponsiveScale } from '../../utils';

const style = {
    fontSize: getResponsiveScale(4),
    fontFamily: 'monospace',
    color: '#ffffff',
    align: 'center'
};

export default class Counter extends Phaser.GameObjects.Text {
    constructor(scene: Phaser.Scene) {
        super(scene, scene.cameras.main.centerX, scene.cameras.main.centerY / 2, '0', style);

        this.scene = scene;

        this.scene.add.existing(this);

        this.setOrigin(0.5, 0.5);
        this.setDepth(3);
    }

    setCount(num: number): Promise<any> {
        return new Promise(resolve => {
            const newValue = parseInt(this.text) + num;

            this.setText(newValue.toString());

            this.scene.tweens.add({
                targets: this,
                duration: 100,
                scaleX: 1.5,
                scaleY: 1.5,
                repeat: 0,
                yoyo: true,
                onComplete: resolve
            });
        });
    }

    resetState(): void {
        this.setText('0');
    }
}
