// @flow

import React, { Component } from 'react';
import './App.css';

import Game from './classes/Game';

type Props = {};

class App extends Component<Props> {
    gameContainerRef: Object;

    constructor() {
        super();

        this.gameContainerRef = React.createRef();
    }

    componentDidMount() {
        new Game({
            parent: this.gameContainerRef.current
        });
    }

    render() {
        return <div ref={this.gameContainerRef} className="App" />;
    }
}

export default App;
