// @flow
import Phaser from 'phaser';
import { getResponsiveScale, getViewport } from '../../utils';

import Pipe from './Pipe';

export default class PipeGroup extends Phaser.GameObjects.Group {
    constructor(scene: Phaser.Scene) {
        super();

        this.scene = scene;
        this.scale = getResponsiveScale(1);
    }

    _generateRandomPos(): number {
        return getViewport().height / 1.5 + Math.floor((Math.random() * getViewport().height) / 4);
    }

    renderPipeColumn(): void {
        const RANDOM_POS = this._generateRandomPos();

        const pipeUp = new Pipe(this.scene, getViewport().width + 100, RANDOM_POS, 'pipe-up');
        const pipeDown = new Pipe(
            this.scene,
            getViewport().width + 100,
            pipeUp.body.y - pipeUp.height * pipeUp._scaleY - getResponsiveScale(1),
            'pipe-down'
        );

        this.addMultiple([pipeUp, pipeDown], true);
    }

    cleanPipesOutbound(): void {
        this.children.entries.forEach((pipe, _) => {
            if (pipe.x < -pipe.getPipeWidth()) {
                pipe.destroy();
            }
        });
    }

    move(cb: function): void {
        this.children.entries.forEach((pipe, _) => {
            pipe.move(-getResponsiveScale(1.2));

            if (!pipe.passedThrough && pipe.isPipeUp()) {
                if (pipe.x <= getViewport().width / 4 / 2) {
                    pipe.setPassedThrough(true);

                    cb(pipe);
                }
            }
        });
    }

    stop(): void {
        this.children.entries.forEach((pipe, _) => {
            pipe.move(0);
        });
    }
}
