// @flow
import Phaser from 'phaser';
import { RELATIVE_VALUE, getViewport, generateTileSpriteScale } from '../../utils';

const dimension: {
    rawWidth: number,
    width: number,
    height: number
} = {
    rawWidth: 276,
    width: getViewport().width,
    height: 109
};

export default class Sky extends Phaser.GameObjects.TileSprite {
    constructor(scene: Phaser.Scene) {
        super(scene, 0, dimension.width, dimension.width, dimension.height, 'sky');

        scene.add.existing(this);

        this.setOrigin(0, 0);
        this.setScale(generateTileSpriteScale(dimension.rawWidth));
        this.setY(getViewport().height - this.height * this._scaleY);
    }

    move(): void {
        this.setTilePosition(this.tilePositionX + 1 / RELATIVE_VALUE);
    }

    setPositionByRef(ref: Phaser.GameObjects.Sprite): void {
        this.setY(getViewport().height - this.height * this._scaleY - ref.height * ref._scaleY);
    }
}
