// @flow
export const RELATIVE_VALUE = 4;

type Window = {
    innerWidth: number,
    innerHeight: number
};

declare var window: Window;

export const getViewport = () => {
    const viewport: { width: number, height: number } = {
        width: window.innerWidth,
        height: window.innerHeight
    };

    return viewport;
};

export const getResponsiveValue = (value: number = 0) => {
    return (value + getViewport().height) / RELATIVE_VALUE;
};

export const getResponsiveScale = (value: number = 1) => {
    return getViewport().height / value / RELATIVE_VALUE;
};

export const generateTileSpriteScale = (dimension: number) => {
    const scaleSize = getViewport().height / dimension;

    if (scaleSize > 1 && scaleSize < 1.5) {
        return scaleSize;
    }

    if (scaleSize >= 1.5) {
        return 1.5;
    }

    return 1;
};
