// @flow

type Asset = {
    key: string,
    image: string
};

export default class AssetsLoader {
    assets: Array<Asset>;
    context: any;

    constructor(assets: Array<any>, context: Object) {
        this.assets = assets;
        this.context = context;
    }

    load(): Promise<Array<Asset>> {
        return new Promise(
            (resolve): void => {
                this.assets.forEach((asset: Asset, index: number, arr: Array<Asset>) => {
                    this.context.textures.addBase64(asset.key, asset.image);
                });

                this.context.textures.on('addtexture', (textureName: string) => {
                    if (this.assets[this.assets.length - 1].key === textureName) {
                        resolve(this.assets);
                    }
                });
            }
        );
    }
}
