// @flow
import Phaser from 'phaser';

import base64Images from '../../assets/base64/images';
import AssetsLoader from '../../utils/AssetsLoader';
import { getResponsiveScale } from '../../utils';

import Land from '../GameObjects/Land';
import Sky from '../GameObjects/Sky';
import Bird from '../GameObjects/Bird';
import PipeGroup from '../GameObjects/PipeGroup';

import EventEmitter from '../Events/GlobalEventEmitter';
import Counter from '../GameObjects/Counter';

export default class MainScene extends Phaser.Scene {
    constructor() {
        super('MainScene');

        this.land = null;
        this.sky = null;
        this.pipes = null;
        this.counter = null;
        this.gameLoaded = false;
        this.tapped = false;

        this.emitter = EventEmitter;
    }

    preload(): void {
        this.emitter.addListener('gameover', this.blinkBackground, this);
    }

    async create(): Promise<void> {
        await new AssetsLoader(
            [
                { key: 'bird', image: base64Images.bird },
                { key: 'pipe-down', image: base64Images.pipeDown },
                { key: 'pipe-up', image: base64Images.pipeUp },
                { key: 'land', image: base64Images.land },
                { key: 'sky', image: base64Images.sky }
            ],
            this
        ).load();

        this.sky = new Sky(this);
        this.land = new Land(this);
        this.bird = new Bird(this);
        this.pipes = new PipeGroup(this);
        this.counter = new Counter(this);

        this.sky.setPositionByRef(this.land);
        this.physics.add.collider(this.bird, this.land, this.killByLand, null, this);
        this.physics.add.overlap(this.bird, this.pipes, this.killBypipes, null, this);

        this.time.addEvent({
            delay: getResponsiveScale(0.25) + 1500,
            callback: this.generatePipeRow,
            loop: true
        });

        this.bird.enterIdle();

        this.gameLoaded = true;
    }

    update(): void {
        if (!this.gameLoaded) return;

        if (this.input.activePointer.isDown) {
            if (this.bird.isAlive && this.bird.isIdle) {
                this.bird.leaveIdle();
            }
            if (this.bird.isAlive && !this.bird.isIdle && !this.tapped) {
                this.bird.fly();
                this.tapped = true;
            }
        } else {
            this.tapped = false;
        }

        if (this.bird.isAlive) {
            this.land.move();
            this.sky.move();
            this.pipes.move(this.handlePointUp);

            if (!this.bird.isIdle && this.bird.angle < 40) {
                this.bird.angle += 1;
            }
        } else {
            this.pipes.stop();
        }

        this.pipes.cleanPipesOutbound();
    }

    blinkBackground(): void {
        let counter = 0;

        const interval = setInterval(() => {
            if (counter >= 6) return clearInterval(interval);

            if (counter % 2) {
                this.cameras.main.setBackgroundColor('#51c0c9');
            } else {
                this.cameras.main.setBackgroundColor('#ff0000');
            }

            counter++;
        }, 50);
    }

    generatePipeRow = (): void => {
        if (!this.bird.isAlive || this.bird.isIdle) return;

        this.pipes.renderPipeColumn();
    };

    handlePointUp = (): void => {
        this.counter.setCount(1);
    };

    killByLand(): void {
        if (this.bird.isAlive && !this.bird.hitByPipe) {
            this.emitter.eventEmit('gameover', this);

            this.bird.kill();
            this.bird.hitGround().then(this.resetSpritesState);
        }

        if (!this.bird.isAlive && this.bird.hitByPipe) {
            this.bird.hitGround().then(this.resetSpritesState);
        }
    }

    killBypipes(): void {
        if (this.bird.isAlive) {
            this.emitter.eventEmit('gameover', this);

            this.bird.kill();
            this.bird.hitPipe();
        }
    }

    resetSpritesState = (): void => {
        this.counter.resetState();
        this.bird.initialState();
        this.pipes.clear(true);
    };
}
