// @flow
import Phaser from 'phaser';
import { getViewport, generateTileSpriteScale } from '../../utils';

const dimension: {
    rawWidth: number,
    width: number,
    height: number
} = {
    rawWidth: 336,
    width: getViewport().width,
    height: 106
};

export default class Land extends Phaser.GameObjects.TileSprite {
    constructor(scene: Phaser.Scene) {
        super(scene, 0, getViewport().width, dimension.width, dimension.height, 'land');

        scene.add.existing(this);
        scene.physics.add.existing(this);

        this.setOrigin(0, 0);
        this.setScale(generateTileSpriteScale(dimension.rawWidth));
        this.setY(getViewport().height - this.height * this._scaleY);
        this.setDepth(2);
        this.body.immovable = true;
        this.body.allowGravity = false;
    }

    move(): void {
        this.setTilePosition(this.tilePositionX + 1);
    }

    generateScale(): number {
        const scaleSize = getViewport().height / dimension.rawWidth;

        if (scaleSize > 1 && scaleSize < 1.5) {
            return scaleSize;
        }

        if (scaleSize >= 1.5) {
            return 1.5;
        }

        return 1;
    }
}
