// @flow
import Phaser from 'phaser';

export default class EventEmitter extends Phaser.Events.EventEmitter {
    addListener(eventName: string, callback: () => void, context: any) {
        this.on(eventName, callback, context);
    }

    removeListener(eventName: string, callback: () => void, context: any, once: boolean = false) {
        this.off(eventName, callback, context, once);
    }

    eventEmit(eventName: string, payload: any) {
        this.emit(eventName, payload);
    }
}
