// @flow
import Phaser from 'phaser';
import { getViewport, getResponsiveScale } from '../../utils';

export default class Bird extends Phaser.GameObjects.Sprite {
    constructor(scene: Phaser.Scene) {
        super(scene, getViewport().width / 4 / 2, getViewport().height / 2 / 1.5, 'bird');

        this.scene = scene;
        this.isAlive = true;
        this.isIdle = true;
        this.hitByPipe = null;
        this.tween = null;
        this.initalPosition = {
            x: this.x,
            y: this.y
        };

        this.scene.add.existing(this);
        this.scene.physics.add.existing(this);

        this.setOrigin(0, 0.5);
        this.setScale(getViewport().height / 3 / 100);
        this.setDepth(3);
        this.body.setBounce(0.2);
        this.body.setAllowGravity(false);
    }

    fly(): void {
        if (this.body.y > 0) {
            this.body.setVelocity(0, this.body.height * this._scaleX - getViewport().height);

            this.scene.physics.world.gravity.y = this.body.y + getResponsiveScale(0.1);
        }

        this.angle = -20;
    }

    hitPipe(): Promise<any> {
        this.hitByPipe = true;

        return new Promise(resolve => {
            // when bird dies, show the animation
            this.scene.tweens.add({
                targets: this,
                duration: 200,
                yoyo: true,
                angle: -40,
                repeat: 0,
                onComplete: () => {
                    this.scene.tweens.add({
                        targets: this,
                        duration: 100,
                        angle: 40,
                        repeat: 0,
                        onComplete: resolve
                    });
                }
            });
        });
    }

    hitGround(): Promise<any> {
        this.hitByPipe = null;

        return new Promise(resolve => {
            // when bird dies, show the animation
            this.scene.tweens.add({
                targets: this,
                duration: 200,
                yoyo: false,
                angle: -20,
                repeat: 0,
                onComplete: () => {
                    this.scene.tweens.add({
                        targets: this,
                        duration: 100,
                        angle: 0,
                        repeat: 0,
                        completeDelay: 1000,
                        onComplete: resolve
                    });
                }
            });
        });
    }

    kill(): void {
        this.isAlive = false;
    }

    revive(): void {
        this.isAlive = true;
    }

    enterIdle(): void {
        this.isIdle = true;
        this.body.setAllowGravity(false);
        this.angle = 0;

        this.tween = this.scene.tweens.add({
            targets: this,
            duration: 1000,
            y: getViewport().height / 2,
            repeat: -1,
            yoyo: true,
            ease: 'Linear'
        });
    }

    leaveIdle(): void {
        this.isIdle = false;
        this.body.setAllowGravity(true);
        this.tween.pause();
    }

    initialState(): void {
        this.hitByPipe = false;
        this.isAlive = true;
        this.body.setAllowGravity(false);
        this.angle = 0;
        this.x = this.initalPosition.x;
        this.y = this.initalPosition.y;

        this.enterIdle();
    }
}
