// @flow
import Phaser from 'phaser';
import { getViewport } from '../../utils';

export default class Pipe extends Phaser.GameObjects.Sprite {
    constructor(scene: Phaser.Scene, x: number, y: number, texture: string) {
        super(scene, x, y, texture);

        this.scene = scene;
        this.passedThrough = false;
        this.texture = texture;
        this.scene.add.existing(this);
        this.scene.physics.add.existing(this);

        this.setDepth(1);
        this.setScale(getViewport().height / 3 / 100);
        this.body.setAllowGravity(false);
    }

    setPassedThrough(bool: boolean): void {
        this.passedThrough = bool;
    }

    isPipeUp(): boolean {
        return this.texture === 'pipe-up';
    }

    getPipeWidth(): number {
        return this.width * this._scaleX;
    }

    move(pos: number): void {
        this.body.setVelocityX(pos);
    }
}
